	import noData from '@/components/noData'
	import back from '@/components/back'
	import axios from 'axios'
	export default {
		components: {
			noData,
			back
		},
		data() {
			return {
				selectPatient:{
					PicturePath:"",
					PatientName:"",
					Age:"",
					PatientType:null
				},
				relation:[],
				user: this.$store.state.patient,
			}
		},
		created() {
			const patient = this.$store.state.patient
			if(patient){
				//切换了就诊人
				this.$get('/PlatFormAPI/Category/QueryItemByCategoryId',{
					CategoryId: 16
	            }).then(res => {
	            	this.relation = res
	            	this.selectPatient = this.$store.state.patient
	            })
				
			}else{
				//获取默认就诊人
				this.$get('/PlatFormAPI/Category/QueryItemByCategoryId',{
					CategoryId: 16
				}).then(o => {
	                this.relation = o
	            })
			}
		},
		methods: {
         	goDetail(type){
         		//type:1-血压；2-血糖；3-体脂
         		this.$router.push({
         			path: '/healthItem', 
         			query: {
         				PatientId: this.selectPatient.PatientId,
         				type:type
         			}
         		});
         	},
		}
	}



